* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.logo {
  font-size: 20px;
}
.single-user {
  display: flex;
  justify-content: space-around;
  flex-direction: column;
  align-items: center;
}
.single-user img {
  width: 50px;
  height: 50px;
  border-radius: 50%;
}
.spineer {
  width: 100px;
  height: 100px;
  border: 20px solid rgba(30, 143, 255, 0.514);
  border-radius: 50%;
  border-top-color: rgba(0, 0, 255, 0.699);
  margin: 40vh auto;
  animation: animation 0.75s infinite linear;
}
ul,
li {
  list-style: none;
}
a {
  color: white;
  text-decoration: none;
}
nav {
  display: flex;
  justify-content: space-between;
}
nav ul {
  display: flex;
}
nav ul li:first-child {
  margin-right: 20px;
}
@keyframes animation {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
/*  user */
.user {
  display: flex;
  padding: 5px;
  border: 2px black solid;
  margin: 5px 0 5px 0;
}

.section {
  width: 50%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: flex-start;
}
.section:first-child {
  align-items: center;
}
.section img {
  border-radius: 50%;
  display: block;
  width: 20%;
}
.follow {
  display: flex;
  padding: 5px;
  justify-content: center;
}
.follow button {
  margin: 5px;
}
